export default {
	props: {
		sellerInfo: {
			type: Object,
			default() {
				return {}
			}
		}
	},
	methods: {
		contact() {
			const isLogin = this.$store.getters.isLogin
			if(isLogin) {
				this.$emit("contact", this.sellerInfo)
				return 
			}
			uni.showModal({
				title: '提示',
				content: '聊天功能需要实名认证',
				confirmText: '去认证',
				cancelText: '不了',
				success: (res) => {
					if(res.confirm) {
						this.$emit("toIdentify")
					}
				}
			})
		},
		browserUserInfo(upload) {
			this.$emit('browserUserInfo', upload)
		}
	}
}
